import React, { useState } from 'react';
import $ from 'jquery';
import './App.css';
import './bootstrap-4.4.1-dist/css/bootstrap.min.css';
import unsplash from './css/unsplash-shore.jpg';
import downArrow from './css/keyboard_arrow_down-24px.svg';

// Mix/Mashed because of React error that re-renders same icon twice
import close from './css/menu-24px.svg';
import menu from './css/close-24px.svg';

// Components
import Title from './title';
import Menu from './menu';

// Center
import AboutMe from './aboutMe';
import WorkExperience from './workExperience';
import Portfolio from './portfolio';
import Skills from './skills';
import Education from './education';
import Contact from './contact';

function App() {

  const [menuIcon, setMenuIcon] = useState(close);
  const [menuClosed, setMenuClosed] = useState(true);

  function menuClick(e) {
    e.preventDefault();
    if (menuClosed) {
      setMenuClosed(false);
      setMenuIcon(menu);
      console.log(menuClosed);
      console.log(menuIcon);
    } else {
      setMenuClosed(true);
      setMenuIcon(close);
      console.log(menuClosed);
      console.log(menuIcon);
    }
  }

  function moveWindowDown(e) {
    e.preventDefault();

    // JQuery scroll (hardcoded) to top of center portfolio
    $('html, body').animate({ scrollTop: 900 }, 800);
  }

  function toTop(e) {
    e.preventDefault();
    $('html, body').animate({ scrollTop: 0 }, 800);
  }

  return (
    <>
      {/* Navbar */}
      <nav className="navbar navbar-expand-md navbar-light bg-white sticky-top">
        <div className="menuDiv">
          <button className="btn" type="button" onClick={menuClick}>
            {/* React to press and change icon */}
            <img className="menu" src={menuIcon} alt="Hamburger Icon" />
          </button>
        </div>
        <div className="mx-auto">
          <button className="btn navbar-brand brand" type="button" onClick={toTop}>
            [/AP]
          </button>
        </div>

        <nav className="sidebar"></nav>
      </nav>

      {/* Menu */}
      <Menu menuOpen={menuClosed} />

      <div className="title"><Title /></div>

      <div className="first">
        <img src={unsplash} alt="unsplash" className="background" />
      </div>

      <div className="dropDownArrow">
        <button className="btn" type="button" onClick={moveWindowDown}>
          <img src={downArrow} className="downArrowSVG" />
        </button>
      </div>

      <div className="center" id="center">
        <AboutMe />
        <WorkExperience />
        <Portfolio />
        <Skills />
        <Education />
        <Contact />

        <footer className="page-footer">
          <h5 style={{color: "#61dafb"}}>Made with React</h5>
          <br />
        </footer>
      </div>


    </>
  );
}

export default App;