import React from 'react'
import './tile.css';


export default function tile(props) {
    const { title, description, image, date, languages } = props;
    return (
        <div className="col-sm-4 py-2">
        <div className="shadow-sm p-3 mb-5 bg-white rounded card h-100">
            <div className="card-body">
                <h2 className="card-title">{title}</h2>
                <h5>{date}</h5>
                <div className="card-text">
                <span>
                    <p>Languages</p>
                    {languages.map(languages => (
                        <p className="langBubble" key={languages}>{languages}</p>
                    ))}
                </span>
                <ul className="card-text-desc">
                    {description.map(desc => (
                        <li key={desc}>{desc}</li>
                    ))}
                </ul>
                </div>
            </div>
            <img className="card-img" src={image}/>
        </div>
        </div>
    )
}
