import React from 'react'
import './menu.css'
import $ from 'jquery'
export default function menu(props) {
    const { menuOpen } = props;

    function moveToAbout(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop: 950}, 800);
    }

    function moveToWork(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop: 1400}, 800);
    }

    function moveToPortfolio(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop: 2600}, 800);
    }

    return (
        <div>
        <div className="menuWindow" id="SideMenu" hidden={menuOpen}>
                <ul className="list-group">
                    <li className="list-group-item">
                        <button className="btn btn-text" type="button" onClick={moveToAbout}>About Me</button>
                    </li>
                    <li className="list-group-item">
                        <button className="btn btn-text" type="button" onClick={moveToWork}>Work Experience</button>
                    </li>
                    <li className="list-group-item">
                        <button className="btn btn-text" type="button" onClick={moveToPortfolio}>Portfolio</button>
                    </li>
                    <li className="list-group-item">
                        <button className="btn btn-text">Skills</button>
                    </li>
                    <li className="list-group-item">
                        <button className="btn btn-text">Education</button>
                    </li>
                    <li className="list-group-item">
                        <button className="btn btn-text">Contact</button>
                    </li>
                </ul>
            </div>
        </div>
    )
}
